/* HomePage.css */

/* Container */
.homepage-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(120deg, #c5efdf, #ffffff);
  font-family: Arial, sans-serif;
}

/* Title */
.homepage-title {
  font-size: 36px;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

/* Link Button */
.homepage-link {
  display: inline-block;
  padding: 12px 24px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s;
  margin: 5px;
}

.homepage-link:hover {
  background-color: #0056b3;
}

.homepage-btn {
  display: inline-block;
  padding: 12px 24px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s;
  margin: 5px;
  outline: none;
  border: none;
  cursor: pointer;
}