/* Container Styling */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(120deg, #c5efdf, #ffffff);
  }
  
  /* Login Form Styling */
  .login-form {
    background: linear-gradient(120deg, #ffffff, #c5efdf);
    padding: 40px;
    margin: 15px;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 380px;
    box-sizing: border-box;
    text-align: center;
  }
  
  .login-form h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
    font-weight: 600;
  }
  
  /* Input Group Styling */
  .input-group {
    margin-bottom: 20px;
  }
  
  .input-group label {
    font-size: 14px;
    color: #888;
    text-align: left;
    display: block;
    margin-bottom: 8px;
  }
  
  .input-group input {
    width: 100%;
    padding: 12px;
    font-size: 16px;
    color: #333;
    border: 1px solid #ddd;
    border-radius: 6px;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  .input-group input:focus {
    border-color: #007bff;
  }
  
  .input-group input::placeholder {
    color: #aaa;
  }
  
  /* Button Styling */
  .login-btn {
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    font-weight: 600;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease-in-out;
  }
  
  .login-btn:hover {
    background-color: #0056b3;
    transform: translateY(-2px); /* Button hover effect */
  }
  
  /* Forgot Password Link - Positioned after Login Button */
  .forgot-password {
    text-align: center;
    margin-top: 15px;
  }
  
  .forgot-password-link {
    color: #007bff;
    font-size: 14px;
    text-decoration: none;
  }
  
  .forgot-password-link:hover {
    text-decoration: underline;
  }
  
  /* Redirect Text for Signup */
  .redirect-text {
    font-size: 14px;
    margin-top: 15px;
    color: #666;
  }
  
  .signup-link {
    color: #007bff;
    text-decoration: none;
    font-weight: 600;
  }
  
  .signup-link:hover {
    text-decoration: underline;
  }
  
  /* Error Message Styling */
  .error-message {
    color: #e74c3c;
    font-size: 14px;
    margin-bottom: 15px;
  }
  