/* Signup.css */

/* Container */
.signup-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(120deg, #c5efdf, #ffffff);
  font-family: 'Arial', sans-serif;
}

/* Form */
.signup-form {
  background: linear-gradient(120deg,  #ffffff, #c5efdf);

  padding: 30px;
  /* margin: 15px; */
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

/* Heading */
.signup-form h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

/* Error Message */
.error-message {
  color: #e74c3c;
  font-size: 14px;
  margin-bottom: 15px;
  text-align: center;
}

/* Input Group */
.input-group {
  margin-bottom: 15px;
}

.input-group label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
  color: #555;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.input-group input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  outline: none;
}

/* Submit Button */
.signup-btn {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.signup-btn:hover {
  background-color: #0056b3;
}

.signup-btn:disabled {
  background-color: #b8daff;
  cursor: not-allowed;
}

/* Redirect Text */
.redirect-text {
  font-size: 14px;
  color: #555;
  text-align: center;
  margin-top: 20px;
}

.redirect-text .login-link {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.redirect-text .login-link:hover {
  text-decoration: underline;
}
