.user-info-container {
  max-width: 600px;
  margin: 40px auto;
  padding: 30px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(120deg, #ffffff, #c5efdf);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1),
    -4px -4px 10px rgba(255, 255, 255, 0.7);
  font-family: "Arial", sans-serif;
}

.user-info-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  color: #444;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
}

.user-info-detail {
  font-size: 18px;
  margin: 12px 0;
  color: #555;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
}

.user-info-detail:last-child {
  border-bottom: none;
}

.user-info-container p span {
  font-weight: bold;
  color: #222;
}
